body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box; }

.App {
  margin: 1.5rem 3rem; }
  .App .title {
    margin-bottom: 3rem; }

.cards-group,
.card-group-item {
  padding: 0;
  list-style: none; }

.card-group {
  width: 100%;
  margin-bottom: 3rem;
  position: relative;
  height: 250px; }

.card-group-items {
  margin: 0;
  padding: 0;
  list-style: none;
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-transform: translate(-8px, -8px);
          transform: translate(-8px, -8px); }

.card-group-item {
  position: absolute;
  width: 100%; }
  .card-group-item.active {
    z-index: 0 !important; }

.card-pagination {
  position: absolute;
  bottom: 0;
  text-align: center;
  width: 100%; }
  .card-pagination button {
    background-color: #03a9f4;
    width: 20px;
    height: 20px;
    border-radius: 100%;
    border: 0 none;
    margin: 0 0.5rem;
    padding: 0; }

.card-group-item:nth-of-type(1) {
  -webkit-transform: translate(8px, 8px);
          transform: translate(8px, 8px);
  z-index: -1; }

.card-group-item:nth-of-type(2) {
  -webkit-transform: translate(16px, 16px);
          transform: translate(16px, 16px);
  z-index: -2; }

.card-group-item:nth-of-type(3) {
  -webkit-transform: translate(24px, 24px);
          transform: translate(24px, 24px);
  z-index: -3; }

.card-group-item:nth-of-type(4) {
  -webkit-transform: translate(32px, 32px);
          transform: translate(32px, 32px);
  z-index: -4; }

.card-group-item:nth-of-type(5) {
  -webkit-transform: translate(40px, 40px);
          transform: translate(40px, 40px);
  z-index: -5; }

.card-group-item:nth-of-type(6) {
  -webkit-transform: translate(48px, 48px);
          transform: translate(48px, 48px);
  z-index: -6; }

.card-group-item:nth-of-type(7) {
  -webkit-transform: translate(56px, 56px);
          transform: translate(56px, 56px);
  z-index: -7; }

.card-group-item:nth-of-type(8) {
  -webkit-transform: translate(64px, 64px);
          transform: translate(64px, 64px);
  z-index: -8; }

.card-group-item:nth-of-type(9) {
  -webkit-transform: translate(72px, 72px);
          transform: translate(72px, 72px);
  z-index: -9; }

.card {
  background-color: #fff;
  width: 100%;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.2);
  border-radius: 0.5rem;
  padding: 1rem; }
  .card-title {
    margin-bottom: 0; }
  .card-subtitle {
    color: #666; }
  .card-description {
    text-align: center;
    font-size: 3.5rem;
    margin: 0 0 1rem; }

